import { Link } from 'react-router-dom';
export const AuthConnexion = () => {
    return <form>
        <section className="formSegment">
            <label>Pseudo ou Mail</label>
            <input type="text" placeholder="Ex: AaZz ou monmail@gmail.com" />
        </section>
        <section className="formSegment">
            <label>Mot de passe</label>
            <input type="text" placeholder="Oh" />
        </section>
        <div className="formBtn">
            <button className='semiRounded'>Connexion</button>
        </div>
        <div className='formOtherAuth'>
            <p>Vous n'avez pas de compte ? <Link to='/Authentification/Inscription'>Inscription</Link> </p>
        </div>
    </form>
}