import '../Assets/styles/publication.css';
import { LastJobsPosted, MostRatedPost, MostViewedPost } from '../RoutesSubComponents/PublicationsComponents';

const Publications = () => {
    return <div className="publicationsPart">
        <section className="publicationsPart-title">
            <h2>Retrouvez ici les dernières offres d'emplois,les profils de recruteur ou candidats
                les plus suivis ou encore les mieux noté </h2>
            <img src={require('../Assets/images/illustrations/annonces1.svg').default} />
        </section>

        <section className="publicationsPart-categorys">
            <LastJobsPosted />
            <MostViewedPost />
            <MostRatedPost />
        </section>
    </div>
}

export default Publications;