import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useRef } from 'react/cjs/react.development';
import { Select } from '../GlobalComponents/form';
export const AuthInscription = () => {
    const [inscriptionFields, setInscriptionFields] = useState({
        utilisateur: '',
        pseudo: '', mail: '', sex: ''
    })
    const handleFieldChange = (event) => {
        const name = event.target.name;
        const value = event.target.value.trim();

        setInscriptionFields({ ...inscriptionFields, [name]: value });
    }

    const [otherInscriptionFields, setOIF] = useState(null);

    const dispatchOIF = () => {
        if (inscriptionFields.utilisateur == "candidat") {
            return <IsCandidate props={{ handleFieldChange }} />
        } else if (inscriptionFields.utilisateur == "recruteur") {
            return <IsEmployeur props={{ handleFieldChange }} />
        }
        else if (inscriptionFields.utilisateur == "vip") {
            return <IsVIP props={{ handleFieldChange }} />
        }
    }

    useEffect(() => setOIF(dispatchOIF()), [inscriptionFields.utilisateur])


    return <form>
        <section className="formSegment">
            <label>Je suis ...</label>
            <select name='utilisateur' defaultValue=''
                onChange={(event) => handleFieldChange(event)}>
                <option value='' disabled>Choisir....</option>
                <option value="candidat">Un(e) candidat(e)</option>
                <option value="recruteur">Un(e) recruteur(e) </option>
                <option value="vip">Un(e) apporteur(se) d'affaire(VIP) </option>
            </select>
        </section>
        {otherInscriptionFields}
        <section className="formSegment">
            <label>Mot de passe</label>
            <input type='password' placeholder='Ex: MonMotdepasse' />
        </section>
        <section className="formSegment">
            <label>Confirmation</label>
            <input type='password' placeholder='MonMotdepasse' />
        </section>
        <div className="formBtn">
            <button className='semiRounded'>Connexion</button>
        </div>
        <div className='formOtherAuth'>
            <p>Vous avez deja un compte ? <Link to='/Authentification/Connexion'>Connexion</Link> </p>
        </div>
    </form>
}


const IsCandidate = ({ props }) => {
    const handleFieldChange = props;
    const sexes = [
        { value: 'Homme', ph: 'Homme' },
        { value: 'Femme', ph: 'Femme' }]
    return <>
        <section className="formSegment">
            <label>Pseudo</label>
            <input type="text" placeholder="Ex: AaZz..." />
        </section>
        <section className="formSegment">
            <label>Mail</label>
            <input type="text" placeholder="Ex: monMail@gmail.com" />
        </section>
        <section className="formSegment">
            <label>Sexe</label>
            <Select props={{
                name: 'sexe', defaultValue: '',
                id: '', options: sexes, onChange: true,
                handleChange: handleFieldChange
            }} />
        </section>
    </>
}

const IsEmployeur = ({ props }) => {
    const handleFieldChange = props;
    const secteurActivités = [
        { value: 'Commerce', ph: 'Commerce' },
        { value: 'Prestation de service', ph: 'Prestation de service' },
        { value: 'Banque et ou Assurance', ph: 'Banque et ou Assurance' }]
    return <>
        <section className="formSegment">
            <label>Nom de l'entreprise</label>
            <input type="text" placeholder="Ex: AaZz..." />
        </section>
        <section className="formSegment">
            <label>Mail de l'entreprise</label>
            <input type="text" placeholder="Ex: mailRecruteur@mail.com" />
        </section>
        <section className="formSegment">
            <label>Secteur d'activité</label>
            <Select props={{
                name: 'activités', defaultValue: '',
                id: '', options: secteurActivités, onChange: true,
                handleChange: handleFieldChange
            }} />
        </section>
    </>
}

const IsVIP = ({ props }) => {
    const handleFieldChange = props;
    return <>
        <section className="formSegment">
            <label>Nom</label>
            <input type="text" placeholder="Ex: AaZz..." />
        </section>
        <section className="formSegment">
            <label>Prénom</label>
            <input type="text" placeholder="Ex: AaZz..." />
        </section>
        <section className="formSegment">
            <label>Mail</label>
            <input type="text" placeholder="Ex: affaires@mail.com" />
        </section>
    </>
}